import swal from 'sweetalert';
import AJAX from './class.AJAX';
import Abstract from './class.Abstract';
import FormValidator from './class.FormValidator';
import { qs } from '../dom-helpers';

class ContactForm {
  constructor() {
    if (!qs('.callback__form')) return;

    this.form = qs('.callback__form');
    this.submitButton = qs('button[type="submit"]');

    this._run();
  }

  _run() {
    try {
      this._handleSubmit();
    } catch (e) {
      console.error(e); // eslint-disable-line
    }
  }

  _handleSubmit() {
    const _this = this;

    this.form.addEventListener('submit', (e) => {
      e.preventDefault();

      if (!this._isFormValid()) return;

      const data = new FormData(this.form);
      AJAX.post({
        data: Abstract.formDataToObject(data),
        url: './mail.php',
        beforeSend() {
          _this.submitButton.disabled = true;
        },
        complete() {
          _this.submitButton.disabled = false;
        },
        success(response) {
          if (response.success) {
            return swal({
              title: '!',
              text: '   ',
              icon: 'success',
            });
          }

          return swal({
            title: '!',
            text: '   ',
            icon: 'error',
          });
        },
      });
    });
  }

  _isFormValid() {
    return new FormValidator().isValid(this.form, {
      name: {
        presence: {
          message: '^  ',
        },
      },
      email: {
        presence: {
          message: '^  email',
        },
        email: {
          message: '^,  email    user@example.com',
        },
      },
      website: {
        presence: {
          message: '^    ',
        },
        url: {
          message: '^,      https://example.com',
        },
      },
      message: {
        presence: {
          message: '^  ',
        },
      },
    });
  }
}

export default ContactForm;
